<template>
  <label
    :for="id + '_button'"
    :class="{ active: isActive }"
    class="toggle__button"
  >
    <span class="toggle__label">{{ label }}</span>
    <input
      type="checkbox"
      :disabled="disabled"
      :id="id + '_button'"
      @click="onToggle"
    />
    <span class="toggle__switch"></span>
  </label>
</template>

<script>
export default {
  name: "ToggleButton",
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
    defaultState: {
      type: Boolean,
      default: false,
    },
    locked: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    this.id = this._uid;
    this.currentState = this.defaultState;
  },
  data() {
    return {
      id: null,
      currentState: true,
    };
  },
  computed: {
    isActive() {
      return this.currentState;
    },
    enableText() {
      return this.labelEnableText;
    },
    disabledText() {
      return this.labelDisableText;
    },
  },
  methods: {
    onToggle() {
      if (this.locked) return;
      this.currentState = !this.currentState;
      this.$emit("toggle", this.currentState);
    },
  },
  watch: {
    defaultState() {
      this.currentState = Boolean(this.defaultState);
    }
  },
};
</script>

<style scoped>
.toggle__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  cursor: pointer;
  padding: 5px 0;
  margin-bottom: 35px;
}

.toggle__button input[type="checkbox"] {
  opacity: 0;
  position: absolute;
  width: 1px;
  height: 1px;
}

.toggle__button .toggle__switch {
  display: inline-block;
  height: 28px;
  border-radius: 12px;
  width: 56px;
  background: #bfcbd9;
  box-shadow: inset 0 0 1px #bfcbd9;
  position: relative;
  margin-left: 10px;
  transition: all 0.25s;
}

.toggle__button .toggle__switch::after,
.toggle__button .toggle__switch::before {
  content: "";
  position: absolute;
  display: block;
  height: 34px;
  width: 34px;
  border-radius: 50%;
  left: 0;
  top: -3px;
  transform: translateX(0);
  transition: all 0.25s cubic-bezier(0.5, -0.6, 0.5, 1.6);
}

.toggle__button .toggle__switch::after {
  background: #4d4d4d;
  box-shadow: 0 0 1px #666;
}

.toggle__button .toggle__switch::before {
  background: #4d4d4d;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.1);
  opacity: 0;
}

.active .toggle__switch {
  background: #BBDCFF;
  box-shadow: inset 0 0 1px #BBDCFF;
}

.active .toggle__switch::after,
.active .toggle__switch::before {
  transform: translateX(40px - 18px);
}

.active .toggle__switch::after {
  left: 23px;
  background: #4A90E2;
  box-shadow: 0 0 1px #4A90E2;
}

.toggle__label {
  font-size: 20px;
}
</style>