<template>
  <div class="settings">
    <div class="settings-title">Режим тренировки</div>
    <div class="toggles">
      <ToggleButton
        label="Подсвечивать возможные ходы"
        :defaultState="highlightMoves"
        @toggle="onToggleHighlightMoves"
      >
      </ToggleButton>
      <ToggleButton
        label="Допускать только корректные ходы"
        :defaultState="onlyCorrectMoves"
        @toggle="onToggleOnlyCorrectMoves"
      >
      </ToggleButton>
      <ToggleButton
        label="Показывать историю ходов"
        :defaultState="showHistory"
        @toggle="onToggleShowHistory"
      >
      </ToggleButton>
    </div>
    <div class="position-select">
      <PositionSelect />
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import ToggleButton from './ToggleButton'
import PositionSelect from './PositionSelect'
import Types from './../store/modules/chessboard/types'

export default {
  name: 'Settings',
  components: {
    ToggleButton,
    PositionSelect
  },
  computed: {
    ...mapGetters({
      highlightMoves: Types.getters.GET_HIGHLITE_MOVES,
      onlyCorrectMoves: Types.getters.GET_ONLY_CORRECT_MOVES,
      showHistory: Types.getters.GET_SHOW_HISTORY
    }),
  },
  methods: {
    ...mapActions({
      onToggleHighlightMoves: Types.actions.TOGGLE_HIGHLITE_MOVES,
      onToggleOnlyCorrectMoves: Types.actions.TOGGLE_ONLY_CORRECT_MOVES,
      onToggleShowHistory: Types.actions.TOGGLE_SHOW_HISTORY
    }),
  },
};
</script>

