<template>
  <div>
    <AppHeader @logo-click="onLogoClick"/>
    <Start v-if="isStartState" @start="onStart"></Start>
    <Game v-if="isGameState"></Game>
  </div>
</template>

<script>
import AppHeader from './components/AppHeader'
import Game from './components/Game'
import Start from './components/Start'

export default {
  name: 'app',
  components: {
    Game,
    Start,
    AppHeader
  },
  data() {
    return {
      state: 'start',
    };
  },
  computed: {
    isGameState() {
      return this.state === 'game';
    },
    isStartState() {
      return this.state === 'start';
    },
  },
  methods: {
    onStart() {
      this.state = 'game';
    },
    onLogoClick() {
      this.state = 'start';
    }
  },
};
</script>

