<template>
  <div>
    <div
      id="board"
      class="board"
    >
    </div>
    <div v-if="isArbitraryPosition">
      <button type="button" class="clear-button" @click="clearBoard">Очистить доску</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Chess from 'chess.js'
import $ from 'jquery'
import Types from './../store/modules/chessboard/types'

export default {
  name: 'ChessBoard',
  data() {
    return {
      board: null,
      game: new Chess(),
    };
  },
  computed: {
    ...mapGetters({
      highlightMoves: Types.getters.GET_HIGHLITE_MOVES,
      onlyCorrectMoves: Types.getters.GET_ONLY_CORRECT_MOVES,
      position: Types.getters.GET_POSITION,
      positionType: Types.getters.GET_POSITION_TYPE
    }),
    isArbitraryPosition() {
      return this.positionType === 'arbitrary';
    }
  },
  mounted() {
    if (window && !window.ChessBoard) {
      console.error('Missing window.ChessBoard!')
      return
    }
    if (window && !window.$) {
      console.error('Missing window.$')
      return
    }

    this.createChessboard();
  },
  methods: {
    removeGreySquares() {
      $("#board .square-55d63").css("background", "");
    },

    greySquare(square) {
      var whiteSquareGrey = "#c1cad0";
      var blackSquareGrey = "#8b98a0";

      var $square = $("#board .square-" + square);

      var background = whiteSquareGrey;
      if ($square.hasClass("black-3c85d")) {
        background = blackSquareGrey;
      }

      $square.css("background", background);
    },
    onDragStart(source, piece) {
      // do not pick up pieces if the game is over
      if (this.game.game_over()) return false;

      // or if it's not that side's turn
      if (
        (this.game.turn() === "w" && piece.search(/^b/) !== -1) ||
        (this.game.turn() === "b" && piece.search(/^w/) !== -1)
      ) {
        return false;
      }
    },
    onDrop(source, target) {
      this.logMove(target);
      this.removeGreySquares();

      // see if the move is legal
      var move = this.game.move({
        from: source,
        to: target,
        promotion: "q", // NOTE: always promote to a queen for example simplicity
      });
      
      // illegal move
      if (move === null) return "snapback";
    },
    onMouseoverSquare(square) {
      if (!this.highlightMoves) return;

      // get list of possible moves for this square
      var moves = this.game.moves({
        square: square,
        verbose: true,
      });

      // exit if there are no moves available for this square
      if (moves.length === 0) return;

      // highlight the square they moused over
      this.greySquare(square);

      // highlight the possible squares for this piece
      for (var i = 0; i < moves.length; i++) {
        this.greySquare(moves[i].to);
      }
    },
    onMouseoutSquare() {
      this.removeGreySquares();
    },
    onSnapEnd() {
      this.board.position(this.game.fen());
    },
    onChange(oldPos, newPos) {
      this.$store.dispatch(Types.actions.UPDATE_POSITION, window.Chessboard.objToFen(newPos));
    },
    createChessboard() {
      if (this.positionType === 'start') {
        this.$store.dispatch(Types.actions.UPDATE_POSITION, '');
        this.$store.dispatch(Types.actions.CLEAR_MOVES);
      }

      let config = {
        pieceTheme: 'js/chessboardjs/img/chesspieces/wikipedia/{piece}.png',
        draggable: true,
        onChange: this.onChange,
      }

      const highlightAndRestrict = {
        onDragStart: this.onDragStart,
        onDrop: this.onDrop,
        onMouseoutSquare: this.onMouseoutSquare,
        onMouseoverSquare: this.onMouseoverSquare,
        onSnapEnd: this.onSnapEnd,
      }

      switch(this.positionType) {
        case 'start':
          config = {
            ...config,
            position: this.position ? this.position : 'start',
            onDrop: (src, target) => this.logMove(target)
          };
          if (this.onlyCorrectMoves) {
            config = {
              ...config,
              ...highlightAndRestrict
            }
          }
          break;
        case 'fen':
          break;
        case 'arbitrary':
          config = {
            ...config,
            dropOffBoard: 'trash',
            sparePieces: true
          }
          break;
      }

      this.board = window.Chessboard("board", config);

      if (this.position) {
        this.board.position(this.position)
      }
    },
    logMove(value) {
      this.$store.dispatch(Types.actions.PUSH_MOVE, value)
    },
    clearBoard() {
      this.$store.dispatch(Types.actions.CLEAR_MOVES);
      this.board.clear();
    }
  },
};
</script>

<style scoped>
.board {
  width: 480px;
  height: 480px;
  margin-bottom: 50px;
}

.clear-button {
    display: block;
    background-color: transparent;
    margin: 0 auto;
    width: 248px;
    height: 43px;
    border-radius: 28px;
    border: solid 2px #ff7708;
    font-size: 18px;
    color: #ff7708;
    cursor: pointer;
}

.clear-button:hover {
    color: #ffffff;
    background-color: #ff7708;
}

.clear-button:focus {
  outline: 0;
}
</style>