import Types from './types'

const initialState = {
  highlightMoves: true,
  onlyCorrectMoves: true,
  showHistory: true,
  moves: [],
  position: '',
  positionType: 'start'   // possible: start, fen, arbitrary
}

const state = { ...initialState }

const actions = {
  [Types.actions.TOGGLE_HIGHLITE_MOVES]({ commit, state }) {
    commit(Types.actions.SET_POSITION_TYPE, 'start');
    if (!state.onlyCorrectMoves) {
      commit(Types.mutations.SET_ONLY_CORRECT_MOVES, true);
    }
    commit(Types.mutations.SET_HIGHLITE_MOVES, !state.highlightMoves)
  },
  [Types.actions.TOGGLE_ONLY_CORRECT_MOVES]({ commit, state }) {
    commit(Types.actions.SET_POSITION_TYPE, 'start');
    if (state.onlyCorrectMoves) {
      commit(Types.mutations.SET_HIGHLITE_MOVES, false);
    }
    commit(Types.mutations.SET_ONLY_CORRECT_MOVES, !state.onlyCorrectMoves)
  },
  [Types.actions.TOGGLE_SHOW_HISTORY]({ commit, state }) {
    commit(Types.actions.SET_POSITION_TYPE, 'start');
    commit(Types.mutations.SET_SHOW_HISTORY, !state.showHistory)
  },
  [Types.actions.SET_ONLY_CORRECT_MOVES]({ commit }, value) {
    commit(Types.mutations.SET_ONLY_CORRECT_MOVES, value)
  },
  [Types.actions.UPDATE_POSITION]({ commit }, value) {
    commit(Types.mutations.SET_POSITION, value)
  },
  [Types.actions.PUSH_MOVE]({ commit }, value) {
    commit(Types.mutations.PUSH_MOVE, value)
  },
  [Types.actions.SET_POSITION_TYPE]({ commit }, value) {
    if (value !== 'start') {
      commit(Types.mutations.SET_HIGHLITE_MOVES, false);
      commit(Types.mutations.SET_ONLY_CORRECT_MOVES, false);
      commit(Types.mutations.SET_SHOW_HISTORY, false);
    }
    commit(Types.mutations.SET_POSITION_TYPE, value)
  },
  [Types.actions.CLEAR_MOVES] ({ commit }) {
    commit(Types.mutations.CLEAR_MOVES)
  }
}

const mutations = {
  [Types.mutations.SET_HIGHLITE_MOVES](state, value) {
    state.highlightMoves = value
  },
  [Types.mutations.SET_ONLY_CORRECT_MOVES](state, value) {
    state.onlyCorrectMoves = value
  },
  [Types.mutations.SET_SHOW_HISTORY](state, value) {
    state.showHistory = value
  },
  [Types.mutations.SET_POSITION](state, value) {
    state.position = value
  },
  [Types.mutations.SET_POSITION_TYPE](state, value) {
    state.positionType = value
  },
  [Types.mutations.PUSH_MOVE](state, value) {
    state.moves.push(value)
  },
  [Types.mutations.CLEAR_MOVES](state) {
    state.moves = []
  }
}

const getters = {
  [Types.getters.GET_HIGHLITE_MOVES]: ({ highlightMoves }) => highlightMoves,
  [Types.getters.GET_ONLY_CORRECT_MOVES]: ({ onlyCorrectMoves }) => onlyCorrectMoves,
  [Types.getters.GET_SHOW_HISTORY]: ({ showHistory }) => showHistory,
  [Types.getters.GET_POSITION]: ({ position }) => position,
  [Types.getters.GET_POSITION_TYPE]: ({ positionType }) => positionType,
  [Types.getters.GET_MOVES]: ({ moves }) => () => [...moves] // return fx, prevent caching
}

export default {
  state,
  mutations,
  actions,
  getters
};